import Vue from 'vue'
import Router from 'vue-router'
import leftBar from '@/components/structure/leftBar'
import topBar from '@/components/structure/topBar'
import mainSpace from '@/components/structure/mainSpace'
import filePath from '@/components/structure/filePath'
import bottomBar from '@/components/structure/bottomBar'
import login from '@/components/login'
import main from '@/components/main'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/',
      name: 'Home',
      component: main,
      children: [
        {
          path: 'home',
          components: {
            myTopBar: topBar,
            myLeftBar: leftBar,
            myMainSpace: mainSpace,
            myFilePath: filePath,
            myBottomBar: bottomBar
          }
        }
      ]
    }
  ]
})



