<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
</style>

<script>
  export default {
    name: "leftBar",
    data() {
      return {}
    },
    methods: {}
  }
</script>





