              <el-dropdown-item>修改信息</el-dropdown-item>
              <el-dropdown-item>系统设置</el-dropdown-item>
              <el-dropdown-item divided>切换用户</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </td>
        <router-view/>
      </tr>
    </table>
  </div>
</template>

<script>
  export default {
    name: "topBar"
  }
</script>

<style scoped>

  #name {
    font-family: Ubuntu, monospace;
    color: aliceblue;
    font-size: 25px;
  }

  #username {
    font-family: Ubuntu, monospace;
    color: aliceblue;
    font-size: 15px;
  }
</style>




