<!--      @current-change="test"
-->
<template>
  <div oncontextmenu="self.event.returnValue=false">
    <el-table
      :data=$store.state.fileList
      stripe
      height="690"
      align="left"
      highlight-current-row
      :default-sort="{prop: 'name', order: 'descending'}"
      @row-click="selectOne"
      @row-dblclick="openFile"
      @row-contextmenu="contextmenu"
      style="width: 100%">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label="文件名"
        width="350">
        <template slot-scope="scope">
          <img src="../../assets/sysicon/dir_minix.png" style="margin-right: 10px"/>
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="fileType"
        label="类型"
        width="180">
      </el-table-column>
      <el-table-column
        prop="fileLength"
        label="大小"
        width="180">
      </el-table-column>
      <el-table-column
        prop="permissions"
        label="权限"
        width="180">
      </el-table-column>
      <el-table-column
        prop="lastModifiedTime"
        label="修改日期"
        width="180">
      </el-table-column>
    </el-table>
    <div  id="menu" class="el-popover" align="left">
      <el-popover
        placement="right"
        width="150"
        trigger="hover" align="left">
        <el-dropdown-item>文件夹</el-dropdown-item>
        <el-dropdown-item>文本文档</el-dropdown-item>
        <el-dropdown-item>压缩文件</el-dropdown-item>
        <el-dropdown-item slot="reference"><span>新建</span><i class="el-icon-arrow-right"></i></el-dropdown-item>
      </el-popover>
      <el-dropdown-item>复制</el-dropdown-item>
      <el-dropdown-item>粘贴</el-dropdown-item>
      <el-dropdown-item>剪切</el-dropdown-item>
      <el-dropdown-item>重命名</el-dropdown-item>
      <el-dropdown-item>下载</el-dropdown-item>
      <el-dropdown-item>属性</el-dropdown-item>
    </div>
  </div>
</template>

<script>
  import store from '../../store'

  export default {
    name: "file",
    methods: {
      openFile(row, event, column) {
        if (row.directory) {
          store.state.filePathName = row.path;
          store.state.oldPath = false;
          store.commit('myLoadData')
        } else {
          alert(row.fileType)
        }
      },
      contextmenu(row, event, column) {
        //获取我们自定义的右键菜单

        let menu = document.querySelector("#menu");
//根据事件对象中鼠标点击的位置，进行定位
        menu.style.left = event.clientX + 'px';
        menu.style.top = event.clientY + 'px';
//改变自定义菜单的宽，让它显示出来
        menu.style.display='inline';
      },
      selectOne(){
        let menu = document.querySelector("#menu");
        menu.style.display='none';
      }
    }
  }
</script>

<style scoped>
  #menu {
    display: none;
    width: fit-content; /*设置为0 隐藏自定义菜单*/
    height: fit-content;
    overflow: hidden; /*隐藏溢出的元素*/
    position: absolute; /*自定义菜单相对与body元素进行定位*/
  }


  .box-card {
    width: 150px;
  }
</style>



