<template>
  <div class="filePathAll">
    <el-row gutter="5" type="flex" class="row-bg">
      <el-col :span="0.5">
        <div class="grid-content bg-purple">
          <el-button type="info"
                     @click.native="$store.commit('goBack')"><
          </el-button>
        </div>
      </el-col>
      <el-col :span="0.5">
        <div class="grid-content bg-purple-light">
          <el-button type="info" @click.native="$store.commit('goForth')">></el-button>
        </div>
      </el-col>
      <el-col :span="17">
        <div class="grid-content bg-purple-light">
          <el-input lazy v-model.lazy="$store.state.filePathName"
                    @keyup.enter.native="$store.commit('myLoadData')"></el-input>
          <div hidden></div>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="grid-content bg-purple-light">

          <el-input
            placeholder="搜索">
            <i slot="prefix" class="el-input__icon el-icon-search"></i>
          </el-input>
        </div>
      </el-col>
      <el-col :span="0.5">
        <div class="grid-content bg-purple-light">
          <el-button type="info" icon="el-icon-menu"></el-button>
        </div>
      </el-col>
      <el-col :span="0.5">
        <div class="grid-content bg-purple-light">
          <el-button type="info" icon="el-icon-tickets"></el-button>
        </div>
      </el-col>
      <el-col :span="0.5">
        <div class="grid-content bg-purple-light">
          <el-dropdown>
            <el-button type="info" icon="el-icon-setting"></el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$store.commit('showHide')">显示隐藏文件</el-dropdown-item>
              <el-dropdown-item @click.native="$store.commit('notShowHide')">隐藏隐藏文件</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import store from '../../store'

  export default {
    name: "filePath",
    data() {
      return {
        filePathName: "/"
      }
    },
    mounted() {

    }
  }
</script>

<style>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .el-col {
    border-radius: 10px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple {
    background: #d3dce6;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #545c64;
  }
</style>



