import Vue from 'vue'
import Vuex from 'vuex'
import {loadData} from '../api'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    //用户信息
    userName:'',


    //文件浏览器缓存
    filePathName: '/',
    fileList: '!(>_<)',
    hide: 'true',
    oldPath: false,
    historyIndex: 0,
    history: []


    //>>
  },
  mutations: {
    myLoadData(state) {
      loadData(state.filePathName).then((result) => {
        state.fileList = result
      });
      if (!state.oldPath) {
        state.history.splice(state.historyIndex,0,state.filePathName);
        state.historyIndex++;
      }
    },
    showHide(state) {
      state.hide = "false";
      loadData(state.filePathName).then((result) => {
        state.fileList = result
      })
    },
    notShowHide(state) {
      state.hide = "true";
      loadData(state.filePathName).then((result) => {
        state.fileList = result
      })
    },
    goForth(state) {

      if (state.oldPath === true) {
        state.historyIndex++;
        if (state.history.length === state.historyIndex) {
          state.oldPath = false;
        }
        state.filePathName = state.history[state.historyIndex - 1];
        loadData(state.history[state.historyIndex - 1]).then((result) => {
          state.fileList = result
        });
      }
    },
    goBack(state) {
      if (state.historyIndex !== 1) {
        state.oldPath = true;
        state.historyIndex--;
        state.filePathName = state.history[state.historyIndex - 1];
        loadData(state.history[state.historyIndex - 1]).then((result) => {
          state.fileList = result
        });

      }
    }
  }
})



