import axios from 'axios';
import store from '../store'
// 接口地址
const BASE_PATH = process.env.API_URL;
// 加载数据
export const loadData = params => {
  return axios.get(BASE_PATH+"/listAllFiles?fileParentPath="+params+"&all="+store.state.hide)
    .then(res =>
      res.data)
};



