<template>
  <div class="bk">
    <div class="ms-login">
      <el-form label-width="0px" class="demo-ruleForm">
        <div class="ms-title">登录</div>
        <el-form-item prop="username">
          <el-input placeholder="username"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" placeholder="password"></el-input>
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="login">登录</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: "login",
    methods:{
      login(){
        this.$router.push({path:'/home'})
      }

    }
  }
</script>

<style scoped>
  .bk {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-size: cover;
    background: url("../assets/login_bk.jpg") no-repeat center center;
  }

  .ms-title {
    margin: -10px auto 20px auto;
    text-align: left;
    font-size: 30px;
    color: #505458;
  }

  .ms-login {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 300px;
    height: 200px;
    margin: -150px 0 0 -190px;
    padding: 40px;
    border-radius: 20px;
    background: #eef6f8;
  }

  .login-btn {
    text-align: center;
  }

  .login-btn button {
    width: 100%;
    height: 36px;
  }
</style>



